/*--------------------- Begin: Loading Page----------------------*/
/*LoadingBox.js - ANALYSING... text container*/
.loadingContainer {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: "Lato", sans-serif;
  z-index: 1;
}

/*LoadingBox.js - ANALYSING... text styling*/
.loader {
  position: absolute;
  top: 40%;
  margin: auto;
  font-size: 5vw;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.5em;
  padding: 0.6vw;
  z-index: 1;
}

/*LoadingBox.js - ANALYSING...colour of normal text*/
.loader span {
  color: #fff;
  mix-blend-mode: difference;
}

/*LoadingBox.js - ANALYSING...colour of box*/
.loader:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 13.3vw;
  height: 100%;
  background: #000;
  animation: animate 5s linear infinite;
}

/*LoadingBox.js - ANALYSING... animation*/
@keyframes animate {
  0% {
    left: 0;
  }
  50% {
    left: calc(100% - 13.3vw);
  }
  100% {
    left: 0;
  }
}

/*LoadingBox.js - Adding sub text that will change every 30 seconds */
.loadingText:before {
  content: "";
  animation: animate1 30s infinite;
}
@keyframes animate1 {
  0% {
    content: "We are going through all the reviews.";
  }
  20% {
    content: "This will take a moment.";
  }
  40% {
    content: "Please be patient as we analyse the reviews.";
  }
  60% {
    content: "This may take a while.";
  }
  100% {
    content: "We are going through all the reviews.";
  }
}
/*--------------------- End: Loading Page----------------------*/

