/* Styling for a column containing a team member card - AboutUs.js*/
.columnTeam {
  float: left;
  width: 33.3%;
  margin-bottom: 1.06vw;
  padding: 0 4vw;
}

/*Making the team member card responsive*/
@media screen and (max-width: 63.3vw) {
  .columnTeam {
    width: 100%;
    display: block;
  }
}

/*Styling for a card that holds the team member info*/
.cardTeam {
  box-shadow: 0 0.2vw 0.5vw 0 rgba(12, 10, 10, 0.897);
  font-family: "Lato", sans-serif;
  color: rgb(88, 87, 87);
  background-color: #fff;
  align-content: center;
  align-items: center;
  padding-bottom: 2.6vw;
}

/*div to hold the name and github link*/
.containerTeam {
  padding: 0 1.3vw;
}

/*styling the team member name*/
.nameTeam {
  padding-top: 2.6vw;
  font-weight: 600;
}

.containerTeam::after,
.rowTeam::after {
  content: "";
  clear: both;
  display: table;
}

/*styling for github links for each team member*/
.githubLinks {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}

/*styling changes when you hover over the github links*/
.githubLinks:hover {
  font-weight: 900;
  color: #004688;
}

/*div that holds the team member image*/
