  border-radius: 0.6rem;
  padding: 2vh;
}
.commentSubmit:hover {
  background-color: #666464;
  box-shadow: 0 0.2vw 0.5vw 0 rgba(12, 10, 10, 0.897);
}
/* =======================BUG FIX=========== */
.center-content {
  margin: 0 auto;
  width: 90%;
}
.sentimentBox {
  padding-top: 15%;
  padding-left: 10%;
  font-family: "Lato" sans-serif;
}
.sentimentInfo {
  font-size: 1.5vw;
  font-weight: 500;
}
.searchBox {
  padding-top: 1vw;
  padding-left: 20vw;
  font-family: "Lato" sans-serif;
  color: white;
}

.remainingReview {
  font-size: 1.6vw;
  font-weight: 500;
  width: 70%;
}
.reviewBox {
  float: left;
  width: 100%;
  height: auto;
  padding: 1.5vw;
  margin-left: 5%;
  margin-top: 3%;
  margin-bottom: 2vw;
  box-shadow: 0 0.2vw 0.5vw 0 rgba(12, 10, 10, 0.897);
}

.reviewText {
  color: #282e34;
  text-align: justify;
  padding-top: 1vw;
}
.RemainingReviewText {
  font-size: 2.5vh;
  text-align: justify;
  padding-top: 1vw;
}

.searchAppsImages {
  border-radius: 0.6vw;
  box-shadow: 0 0.2vw 0.5vw 0 rgba(12, 10, 10, 0.897);
}
#backBtn {
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  color: #000;
  cursor: pointer;
  padding: 15px;
  border-radius: 4px;
}
.sidenav {
  position: sticky;
  position: -webkit-sticky;
  z-index: 100;
  top: 10px;
  height: 95vh;
  background: rgba(5, 5, 5, 0.377);
  overflow-x: hidden;
  overflow-y: auto;
  text-transform: lowercase;
}
/* width */
.sidenav::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.sidenav::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 5px;
}

/* Handle */
.sidenav::-webkit-scrollbar-thumb {
  background: rgba(5, 5, 5, 0.377);
  border-radius: 5px;
}
.sidenav a {
  padding: 0.4vw;
  text-decoration: none;
  font: "Lato" sans-serif 1.3rem;
  font-weight: 600;
  color: #ffffff;
  display: block;
}

.sidenav-title {
  padding: 0.4vw;
  text-decoration: none;
  font: "Lato" sans-serif 1.3rem;
  font-weight: 600;
  color: #000;
  display: block;
}

.sidenav a:hover {
  text-decoration: none;
  font: "Lato" sans-serif 1.3rem;
  color: #130b5e;
}
.errorPageBtn {
  font-size: 1rem;
  font-weight: 600;
  padding: 1vw;
  border-radius: 0.6vw;
  width: 20%;
}
.errorPageBtn:hover {
  background-color: #282e34;
  color: #fff;
}
.search_container {
  columns: 2 auto;
}

/*-----------overall sentiment cards------------*/
.columnSenti {
  float: left;
  width: 40%;
  padding: 0 10px;
  margin: 2vw;
}

/* Remove extra left and right margins, due to padding */
.rowSenti {
  margin: 0 -5px;
}

/* Clear floats after the columns */
.rowSenti:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
  .columnSenti {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

/* Style the counter cards */
.cardSenti {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  color: #282e34;
  background-color: #f1f1f1;
}
.faicon {
  margin-right: 10px;
}

/*------------------user ratings box-----------------*/
.heading {
  font-size: 1.4 rem;
  text-align: left;
}

.fa {
  font-size: 25px;
}

.checked {
  color: #73b9ee;
}

/* Three column layout */
.side {
  float: left;
  width: 15%;
  margin-right: 10px;
  margin-top: 10px;

}

.middle {
  margin-top: 20px;
  float: left;
  width: 60%;
}

/* Place text to the right */
.right {
  text-align: right;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* The bar container */
.bar-container {
  width: 100%;
  background-color: #ccc7c7;
  text-align: center;
  color: white;
}

/* Individual bars */
.bar-5 {
  width: 70%;
  height: 18px;
  background-color: #003396;
}
.bar-4 {
  width: 50%;
  height: 18px;
  background-color: #1750ac;
}
.bar-3 {
  width: 40%;
  height: 18px;
  background-color: #3373c4;
}
.bar-2 {
  width: 30%;
  height: 18px;
  background-color: #5494da;
}
.bar-1 {
  width: 20%;
  height: 18px;
  background-color: #73b9ee;
}

/* Responsive layout - make the columns stack on top of each other instead of next to each other */
@media (max-width: 400px) {
  .side,
  .middle {
    width: 100%;
  }
  .right {
    display: none;
  }
}
